.cloud {
  background: url("../../cloud.svg") no-repeat;
  position: absolute;
  width: 10%;
  height: 10%;

  animation-name: move;
  animation-duration: 45s;
  animation-fill-mode: both;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-delay: 9s;
}
.cloud:nth-of-type(2) {
  top: 175px;
  left: -750px;
  animation-delay: 3s;
}
.cloudJK {
  background: url("../../cloud.svg") no-repeat;
  top: 300px;
  left: 0;
  position: absolute;
  width: 10%;
  height: 10%;

  animation-name: move;
  animation-duration: 55s;
  animation-delay: 2s;
  animation-fill-mode: both;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
@keyframes move {
  0% {
    opacity: 0;
    left: 0;
  }
  20% {
    opacity: 0.1;
  }
  70% {
    opacity: 0.1;
  }
  100% {
    opacity: 0;
    left: 90%;
  }
}
