@keyframes blurEffect {
  0% {
    opacity: 1;
    left: 0;
    filter: blur(10px);
  }
  100% {
    opacity: 1;
  }
}
@keyframes blurEffectBackground {
  0% {
    opacity: 1;
    left: 0;
    backdrop-filter: brightness(0%);
  }
  50% {
    opacity: 1;
    backdrop-filter: brightness(70%) blur(4px) hue-rotate(20deg);
  }
  100% {
    opacity: 1;
    backdrop-filter: brightness(0%);
  }
}
